import styles from './CountryCard.module.css'


const CountryCard = ({ arrData }) => {

    return <li className={styles.card}>
        <span className={styles.name}>{arrData.name}</span>
        <img src={arrData.flag} alt={arrData.name} />
        <div className={styles.info}>
            <span>Населення - {arrData.population}</span>
            <span>Регіон - {arrData.region}</span>
            <span>Столиця - {arrData.capital}</span>
        </div>
    </li>
}

export default CountryCard